<template>
  <table className='table table-striped table-hover'>
        <thead>
            <tr>
                <th>Nombre</th>
                <th>Fecha</th>
                <th>Descripcion</th>
                <th>Monto</th>
            </tr>     
        </thead>
        <tbody>
 
            <tr  v-for="fac in getFacturasAll" :key="fac._id">
                <td>{{fac.socio.nombre}}</td>
                <td>{{fac.fecha.slice(0,10)}}</td>
                <td>{{fac.descripcion}}</td>
                <td>{{fac.monto}}</td>

                <td>
                    <button
                        className='btn btn-secondary'
                        @click="setFactura(fac)"
                        :disabled="getIsLoading"
                    >
                        Editar
                    </button>
                </td>
                <td>
                    <button
                        className='btn btn-danger'
                        @click="deleteFactura(fac._id)"
                        :disabled="getIsLoading"
                    >
                        Eliminar
                    </button>
                </td>

            </tr>

        </tbody>
    
    </table>
    
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex';

export default {
   
    computed:{
        ...mapGetters('facturar',['getFacturasAll','getIsLoading'])
    },
    methods:{
        ...mapActions('facturar',['getFacturas','deleteFactura']),
        ...mapMutations('facturar',['setFactura'])
    },
    created(){
        this.getFacturas()
    }
}
</script>

